	import uniIcon from "@/components/uni-icon/uni-icon.vue"
	import senSetPicker from "@/components/sen-pickerview/picker-view-set.vue"
	export default{
		components:{
			uniIcon,
			senSetPicker
		},
		data(){
			const currentDate = this.getDate({
				format: true
			});
			return{
				requestData:{},
				inputValue : '',
				shixian : false,
				type:'',
				text:'', 
				styleObj:""
			}
		},
		watch:{
			text(val){
				if(this.type==='0'){
					if(val!=''){
					this.styleObj="background:#2FC8D6"
				   }else{
					this.styleObj=""
				   }
				}	
			}
		},
		methods:{
			//通过传来的参数判断是退租请求还是续租请求,改变pages里的title
			onLoad(options){
				this.type=options.key;
				if(options.key==='0'){
					uni.setNavigationBarTitle({
					  title: '退租请求'
					});
					this.requestData={
					  quitDate:'2019-03-28',
					  message:[
					  	'1、提前退租请求将发送至房东进行确认处理',
					  	'2、发送退租请求后，注意查收房东确认消息',
					  	'3、一当提前退租，租房合同终止',
					  	'4、提前退租不予退还押金，务必谨慎'
					  ],
					  reasonTitle:'退租理由',
					  placeholder:'请填写退租理由(工作调动、出国等) 200字以内'
					};
					this.inputValue='2019-03-28';
				}else{
					uni.setNavigationBarTitle({
					  title: '续租请求'
					});
					this.requestData={
						startDate:'2019-03-28',
						endDate:'2020-03-27',
						message:[
							'1、续租请求将发送至房东进行确认',
							'2、发送退租请求后，注意查收房东确认消息',
							'3、由房东操作续租，一当续租成功，新的续租合同即刻生效'
						],
						reasonTitle:'备注',
						placeholder:'续租后新的合同，房租要求等(200字以内)',
					};
					this.inputValue='2020-03-27';
					this.styleObj="background:#2FC8D6"
				}
			},
			submit(){
				// this.isCommited=true;
				// setTimeout(()=>{
				//   this.isCommited =false;
				// }, 1500)
				uni.showToast({
					title:'已提交'
				});
				setTimeout(function(){
                   uni.navigateBack({
                   	delta: 1
                   });
				},1500)
			},
			name : function () {
					this.$refs.setpicker.confirm(this.inputValue);
					this.shixian = true;				
			},
			//取消选择，返回默认状态
			quxiaobutton : function () {
					this.shixian = false;
			},
			//确定日期
			quedingbutton : function(bangdingyear,bangdingmonth,bangdingday){
					this.shixian = false;
					this.inputValue = bangdingyear + "-" + bangdingmonth + "-" + bangdingday;	
			},
			//获取当天日期作为选择日期里的默认日期
			getDate(type) {
				const date = new Date();
				let year = date.getFullYear();
				let month = date.getMonth() + 1;
				let day = date.getDate();
			
				if (type === 'start') {
					year = year - 60;
				} else if (type === 'end') {
					year = year + 2;
				}else if(type === 'now') {
					year = year;
				}
				month = month > 9 ? month : '0' + month;;
				day = day > 9 ? day : '0' + day;
			
				return `${year}-${month}-${day}`;
			},
		}
	}
